.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding-block: 32px 16px;
  padding-inline: 16px;
  font-family: Roboto, sans-serif;
  transition: transform .3s, color .3s;
}

.card__title {
  color: #060b35;
  margin-block: 40px 0;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 18px;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
}

.card__img {
  width: 160px;
  height: 134px;
  margin: auto;
  display: block;
}

.card__product-code {
  color: #616070;
  margin-block: 4px 16px;
  font-size: 10px;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  margin-top: 8px;
  display: flex;
}

.card__price-text {
  color: #616070;
  align-items: center;
  font-size: 12px;
  line-height: 18px;
}

.card__price-sum {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  font-weight: 800;
  transition: background-color .3s, color .3s;
}

.card__button:hover {
  color: #00acdc;
  background: #fff;
  border: 1px solid #00acdc;
}

.stars {
  justify-content: space-between;
  align-items: center;
  height: 16px;
  display: flex;
}

.stars__star {
  background-image: url("star-active.edda4c43.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars__reviews {
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.stars--1 .stars__star:nth-child(n+2), .stars--2 .stars__star:nth-child(n+3), .stars--3 .stars__star:nth-child(n+4), .stars--4 .stars__star:nth-child(n+5), .stars--5 .stars__star:nth-child(n+6) {
  background-image: url("star.822d561e.svg");
}

.header {
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  display: flex;
}

.nav {
  justify-content: flex-end;
  display: flex;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav .nav__item {
  margin-right: 20px;
}

.nav .nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  text-transform: uppercase;
  color: #060b35;
  align-items: center;
  height: 60px;
  text-decoration: none;
  transition: color .3s;
  display: flex;
  position: relative;
}

.nav__link:hover, .is-active, .is-active:hover {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.catalog {
  justify-content: center;
  padding: 50px 40px;
  display: flex;
}

.catalog__grid {
  grid-template-columns: repeat(auto-fit, 200px);
  gap: 46px 48px;
  display: grid;
}

@media (width >= 488px) {
  .catalog__grid {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .catalog__grid {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .catalog__grid {
    grid-template-columns: repeat(4, 200px);
  }
}

body {
  box-sizing: border-box;
  margin: 0;
}
